<template>
    <section>
        <div class="row">
            <div class="col-md-12 padding-t_8">
                <div class="theme-primary partition-full">
                    <span class="p-name text-white">{{ $t('Service') }}</span>
                </div>
            </div>
        </div>

        <div class="border p-3">
            <Form ref="AddService">
                <div class="row">
                    <div class="listing">
                        <label class="ml-3">Work Type:<sup class="text-danger">*</sup></label>
                        <div class="d-flex">
                            <div class="col-lg-4">
                            
                                <Field :name="'WorkType'" v-slot="{ errors }" :rules="{ 'required': true }">
                                    <select :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                        class="form-control level" v-model="workTypeId" name="workTypeId">
                                        <option value>Select Work Type</option>
                                        <option v-for="gitem in WorkTypeData" :value="gitem.value" :key="gitem">
                                            {{ gitem.name }}</option>
                                    </select>

                                    <span v-if="errors[0]" :name="'WorkType' + index" class="invalid-feedback d-block">{{
                                        errors[0] }}</span>
                                </Field>
                            </div>
                            OR
                            <div class="col-lg-4">
                                <button type="button" class="btn btn-info" @click="OpenManagePopup()"><i
                                        class="fa fa-plus pr-2"></i>{{
                                            $t('CreateNewWorkType') }}</button>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="row">
                    <div class="listing mt-3 px-3">
                        <div class="table-responsive">
                            <table class="table table-bordered dt-responsive nowrap mt-0 dataTable no-footer dtr-inline"
                                id="tbContainer">
                                <thead class="thead-bg">
                                    <tr>
                                        <th><span>{{ $t('ServiceName') }}</span></th>
                                        <th><span>{{ $t('Cost') }}</span></th>
                                        <th><span>{{ $t('Markup') }}</span></th>
                                        <th><span>{{ $t('UnitPrice') }}</span></th>
                                        <th><span>{{ $t('ServiceDuration') }}</span></th>
                                        <th><span>{{ $t('Description') }}</span></th>
                                        <th class="text-center single-action"><a href="javascript:;" @click="AddRow"
                                                class="round-icon-small btn-success-light d-block float-none m-auto"
                                                style="line-height: 33px;" id="btnAdd" title="Add"><i
                                                    class="fa fa-plus"></i></a></th>
                                    </tr>
                                </thead>
                                <tbody>

                                    <tr class="mainleveldiv" v-for="(item, index) in Mainleveldiv" :key="index">
                                        <td>
                                            <input type="text" class="form-control" v-model="item.ServiceName"
                                                placeholder="ServiceName" />
                                        </td>
                                        <td>
                                            <input type="number" class="form-control" v-model="item.Cost"
                                                placeholder="Cost" />
                                        </td>
                                        <td>
                                            <input type="number" class="form-control" v-model="item.Markup"
                                                placeholder="Markup" />
                                        </td>
                                        <td>
                                            <input type="number" class="form-control" v-model="item.UnitPrice"
                                                placeholder="UnitPrice" />
                                        </td>
                                        <td>
                                            <input type="number" class="form-control" v-model="item.ServiceDuration"
                                                placeholder="ServiceDuration" />
                                        </td>

                                        <td>
                                            <input type="text" class="form-control" v-model="item.Description"
                                                placeholder="Description" />
                                        </td>

                                        <td class='text-center single-action' v-if="index != 0"><a href='javascript:;'
                                                id='btnRemove' @click="DeleteUser(index)"
                                                class='round-icon-small btn-danger-light d-block float-none m-auto'
                                                title='Remove'><i class='fa fa-trash'></i></a></td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
                <div class="col-12 mt-3">
                    <div class="row flex-row-reverse">
                        <div class="col-lg-6 text-right">
                            <a href="javascript:;" id="aChainTicketSave" class="btn btn-success" @click="SaveData"
                                title="Save"><i class="fa fa-floppy-o pr-2"></i><span>{{ $t('Save') }}</span></a>&nbsp;
                            <RouterLink to="/Ticketing/ServiceListing" title="Cancel" class="btn btn-danger">
                                <i class="fa fa-times pr-2"></i><span>{{ $t('Cancel') }}</span>
                            </RouterLink>
                        </div>
                        <div class="col-lg-6">
                            <div class="col-lg-6">
                                <div class="py-2"><span class="text-danger">{{ $t('MandatoryString') }}</span></div>
                            </div>
                        </div>
                    </div>
                </div>

            </Form>
        </div>
        <AddWorkTypePopup v-if="AddWorkTypePopup"></AddWorkTypePopup>
    </section>
</template>
<script>
import DataServices from "@/services/DataService";
import AddWorkTypePopup from '../../components/Modal/AddWorkTypePopup.vue'
export default {
    components: { AddWorkTypePopup },
    created: function () {
        var vm = this;
        vm.GetDropDownData();
    },
    data() {
        return {
            WorkTypeData: [],
            AddWorkTypePopup: false,
            workTypeId:'',
            Mainleveldiv: [{ ServiceName: '', Cost: '', Markup: '', UnitPrice: '', ServiceDuration: '', Description: '' }],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-warning",
                    text: "<i class='fa fa-save pr-2'></i>Save Of Draft",
                    isVisible: false
                }
            ],
        }
    },
    methods: {
        AddRow: function (e) {
            var vm = this;
            vm.Mainleveldiv.push({
                ServiceName: '', Cost: '', Markup: '', UnitPrice: '', ServiceDuration: '', Description: ''
            })
        },
        GetDropDownData: function () {
            var vm = this;
            var url = `id=&moduleName=TICKETING&type=FSM_WORKTYPE&search=&refId=`;

            DataServices.GetDDLData(url).then(response => {
                if (response.status == 200) {
                    debugger;
                    if (response.data.DATA != undefined || response.data.DATA != null) {
                        response.data.DATA.forEach((item) => {
                            vm.WorkTypeData.push({
                                value: item.value,
                                name: item.name
                            })
                        })
                    }
                }
            })
        },
        OpenManagePopup: function (id) {
            var vm = this;
            vm.AddWorkTypePopup = !vm.AddWorkTypePopup;
        },
        DeleteUser: function (id) {
            var vm = this;
            vm.Mainleveldiv.splice(id, 1);
        },
        SaveData: function () {
            var vm = this;
            vm.$refs.AddService.validate().then(result => {
                if (result) {
                    debugger;
                    var params = `workTypeId=${vm.workTypeId}&serviceDetailData=${JSON.stringify(vm.Mainleveldiv)}`
                    DataServices.AddServiceForm(params).then(response => {
                        if (response.data == true) {
                            vm.ShowAlert('Service has been succesfully added', "success");
                            vm.$router.push({ path: '/Ticketing/ServiceListing' });
                        }
                        else {
                            vm.ShowAlert(vm.$t('GroupHasAdded'), "success");
                        }
                    })
                }
            })
        }
    }
}

</script>